import React, { Component } from 'react'
import { List } from 'antd'
import 'antd/dist/antd.css'
// import { connect } from 'react-redux'
import contract from '../../contracts/contract';

const data = [

];

class MyConf extends (Component) {

  constructor(){
    super()
    this.state={loading: false}
  }

  componentDidMount(){
    //学习conflist/index.js该位置代码进行实现。
    contract.methods.queryConfList()
    .call({from:window.web3.eth.accounts[0]},(err,res)=>{
      //将返回的数组依次压入data中
      this.setState({loading: true});
      for(var i=0;i<res.length;i=i+2){
        data.push({title:res[i],detail:res[i+1]});
      }
    })
    .then(()=>{
      //更新状态，使页面数据重新渲染
      this.setState({loading: false});
    });

    // contract.events.updateNewConf({
    //   filter: {}, 
    //   fromBlock: window.web3.eth.getBlockNumber()
    // }, function(error, event){ })
    // .on('data', function(event){
    //   console.log(event.returnValues);
    // })
    // .on('changed', function(event){
    // })
    // .on('error', console.error);
  }
  
  render() {
    return (
      <List
        itemLayout="horizontal"
        dataSource={data}
        loading={this.state.loading}
        renderItem={item => (
          <List.Item>
            <List.Item.Meta
              title={item.title}
            />
          </List.Item>
        )}
      />
    )
  }
}

export default MyConf